import * as React from 'react';
import { ColumnModel } from 'tubular-common';

interface DataGridBodyShimmerProps {
    columns: ColumnModel[];
    itemsPerPage: number;
}
export const DataGridBodyShimmer: React.FunctionComponent<DataGridBodyShimmerProps> = ({
    columns,
    itemsPerPage,
}: DataGridBodyShimmerProps) => (
    <tbody className='bg-white divide-y divide-gray-200 animate-pulse'>
        {[...Array(itemsPerPage)].map((_row, index) => (
            <tr key={_row}>
                {columns
                    .filter((c) => c.isComputed || c.visible)
                    .map((c) => (
                        <td role='cell' key={c.name} className='px-6 py-4 whitespace-nowrap'>
                            <div className='bg-gray-200'>&nbsp;</div>
                        </td>
                    ))}
            </tr>
        ))}
    </tbody>
);

export default DataGridBodyShimmer;
