import * as React from 'react';
import { ColumnModel } from 'tubular-common';
import { handleFilterChange, onKeyDown } from './utils';

export interface StringFilterProps {
  column: ColumnModel;
  onApply: () => void;
}

export const StringFilter: React.FunctionComponent<StringFilterProps> = ({
  column,
  onApply,
}: StringFilterProps) => (
  <input
    placeholder=""
    defaultValue={column.filterText}
    onKeyDown={onKeyDown(onApply)}
    onChange={handleFilterChange(column)}
    type="text"
    className="pl-1 border border-gray-400 focus:outline-none focus:border-primary block w-full shadow-sm sm:text-s"
  />
);

export default StringFilter;
