import { Menu, Transition } from '@headlessui/react';
import * as React from 'react';
import tw from 'tailwind-styled-components';
import {
  ColumnModel,
  getOperators,
  ColumnDataType,
  CompareOperators,
} from 'tubular-common';
import { DateFilter } from './DateFilter';
import { FilterEditorProps } from './FilterEditorProps';
import { NumericFilter } from './NumericFilter';
import { StringFilter } from './StringFilter';
import { getOperatorIcon, getOperatorText } from './utils';

const getFilterControl = (column: ColumnModel, onApply: () => void) => {
  switch (column.dataType) {
    case ColumnDataType.Numeric:
      return <NumericFilter column={column} onApply={onApply} />;

    case ColumnDataType.String:
      return <StringFilter column={column} onApply={onApply} />;
    case ColumnDataType.Date:
    case ColumnDataType.DateTime:
    case ColumnDataType.DateTimeUtc:
      return <DateFilter column={column} onApply={onApply} />;

    default:
      return null;
  }
};

const Arrow = tw.svg`
h-3
w-3
inline-block
`;

export const StandardFilterEditor: React.FunctionComponent<FilterEditorProps> = ({
  column,
  onApply,
}: FilterEditorProps) => {
  const aColumn = column;
  const [currentOperator, setCurrentOperator] = React.useState(
    column.filterOperator
  );

  const options = getOperators(column).map((row: any) => ({
    key: row.value,
    icon: getOperatorIcon(column.filterOperator),
    text: getOperatorText(row.value, row.title),
  }));

  const handleOperatorClick = (operator: CompareOperators) => {
    setCurrentOperator(operator);
    aColumn.filterOperator = operator;
  };

  return (
    <div className="flex">
      <div className="relative flex justify-center flex-col mr-2">
        <Menu>
          {({ open }: any) => (
            <>
              <Menu.Button className="inline-flex justify-center w-full px-2 py-2 text-sm font-medium leading-5 text-primary transition duration-150 ease-in-out space-x-1 bg-white focus:outline-none hover:bg-primary-hoverLight">
                {getOperatorIcon(currentOperator)}
                <Arrow viewBox="2 2 16 16" fill="currentColor">
                  <path d="M5.293 7.293a1 1 0 011.414 0L10 10.586l3.293-3.293a1 1 0 111.414 1.414l-4 4a1 1 0 01-1.414 0l-4-4a1 1 0 010-1.414z" />
                </Arrow>
              </Menu.Button>
              <Transition
                show={open}
                enter="transition ease-out duration-100"
                enterFrom="transform opacity-0 scale-95"
                enterTo="transform opacity-100 scale-100"
                leave="transition ease-in duration-75"
                leaveFrom="transform opacity-100 scale-100"
                leaveTo="transform opacity-0 scale-95"
              >
                <Menu.Items className="absolute z-10 left-0 w-44 mt-2 origin-top-right bg-white border border-gray-200 divide-y divide-gray-100 shadow-lg outline-none">
                  {options.map((option) => (
                    <Menu.Item key={option.key}>
                      {({ active }: any) => (
                        <a
                          href="#"
                          role="menuitem"
                          onClick={() => handleOperatorClick(option.key)}
                          className="px-2 py-1 block text-xs font-medium text-primary hover:bg-primary-hoverLight"
                        >
                          {getOperatorIcon(option.key)}
                          <span className="text-gray-900 ml-2 text-xs">
                            {option.text}
                          </span>
                        </a>
                      )}
                    </Menu.Item>
                  ))}
                </Menu.Items>
              </Transition>
            </>
          )}
        </Menu>
      </div>
      {getFilterControl(column, onApply)}
    </div>
  );
};

export default StandardFilterEditor;
