import React from 'react';
import tw from 'tailwind-styled-components';
import { TextInputProps } from './TextInputProps';

const TextBox = tw.div`
flex
flex-row
items-center
mt-1
mb-0
focus-within:ring-primary
focus-within:border-primary
focus-within:ring-2
border
border-gray-500 
w-auto 
rounded-sm
${({ error }: TextInputProps) => error && 'bg-red-100 border-red-400'}
`;

const Text = tw.input` 
text-sm 
text-black
md:text-base
bg-transparent
p-1
focus:outline-none
w-full 
flex-grow
`;

const Alert = tw.svg`
flex-initial
right-1
stroke-current
bg-transparent
text-red-400
mx-2
w-5
h-5
${({ error }: TextInputProps) => (error ? 'visible' : 'invisible')}
`;

const TextInput: React.FC<TextInputProps> = ({
    disabled = false,
    placeholder,
    error = false,
    type = 'text',
    ...props
}) => (
    <TextBox error={error}>
        <Text placeholder={placeholder} disabled={disabled} type={type} {...props} />
        <Alert xmlns='http://www.w3.org/2000/svg' fill='none' viewBox='0 0 24 24' error={error}>
            <title>AlertSVG</title>
            <path
                strokeLinecap='round'
                strokeLinejoin='round'
                strokeWidth={2}
                d='M12 9v2m0 4h.01m-6.938 4h13.856c1.54 0 2.502-1.667 1.732-3L13.732 4c-.77-1.333-2.694-1.333-3.464 0L3.34 16c-.77 1.333.192 3 1.732 3z'
            />
        </Alert>
    </TextBox>
);

export default TextInput;
