import * as React from 'react';
import { Select, SelectItem } from '@hornero/ui';

const options: SelectItem[] = [
    { id: 0, value: 5, label: '5' },
    { id: 1, value: 10, label: '10' },
    { id: 2, value: 25, label: '25' },
    { id: 3, value: 50, label: '50' },
];

export const PaginationRowsPerPageDropDown: React.FunctionComponent<any> = ({ itemsPerPage, updateItemsPerPage }) => (
    <>
        <p className='text-xs text-gray-400 mr-1'>View:</p>
        <div className='w-11'>
            <Select
                onChange={(event) => updateItemsPerPage(options[event.currentTarget.selectedIndex].value)}
                datasource={options}
                value={itemsPerPage}
                extraClasses='bg-gray-100 font-bold text-xs h-7'
            />
        </div>
        <p className='text-xs text-gray-400 ml-1'>per page</p>
    </>
);

export default PaginationRowsPerPageDropDown;
