import * as React from 'react';
import { ColumnModel, CompareOperators } from 'tubular-common';

export interface DateFilterProps {
  column: ColumnModel;
  onApply: () => void;
}

const getInitialDates = (column: ColumnModel) => {
  const dates: [Date, Date] = [null, null];

  const startDate = Date.parse(column.filterText);

  if (!Number.isNaN(startDate)) {
    dates[0] = new Date(startDate);
  }

  const toDate = Date.parse(
    column.filterArgument && column.filterArgument[0]
      ? column.filterArgument[0].toString()
      : null
  );

  if (!Number.isNaN(startDate)) {
    dates[1] = new Date(toDate);
  }

  return dates;
};

export const DateFilter: React.FunctionComponent<DateFilterProps> = ({
  column,
}: DateFilterProps) => {
  const columnParameter = column;
  const [dates, setDates] = React.useState(getInitialDates(columnParameter));

  const handleDateChange = (isSecondInput?: boolean) => (
    event: React.ChangeEvent<HTMLInputElement>
  ) => {
    const normalizedDate = event.target.valueAsDate;
    if (isSecondInput) {
      columnParameter.filterArgument = [];
      setDates([dates[0], normalizedDate]);
      columnParameter.filterArgument[0] = normalizedDate
        ? normalizedDate.toISOString()
        : null;
    } else {
      setDates([normalizedDate, dates[1]]);
      columnParameter.filterText = normalizedDate
        ? normalizedDate.toISOString()
        : null;
    }
  };

  const isBetween = columnParameter.filterOperator === CompareOperators.Between;

  return (
    <>
      <input
        placeholder={isBetween ? 'From' : 'Select a date'}
        defaultValue={dates[0] ? dates[0].toString() : ''}
        onChange={handleDateChange()}
        type="date"
        className="pl-1 border border-gray-400 focus:outline-none focus:border-primary block w-full shadow-sm sm:text-s"
      />
      {columnParameter.filterOperator === CompareOperators.Between && (
        <input
          placeholder="To"
          defaultValue={dates[1] ? dates[1].toString() : ''}
          onChange={handleDateChange(true)}
          type="date"
          className="pl-1 border border-gray-400 focus:outline-none focus:border-primary block w-full shadow-sm sm:text-s"
        />
      )}
    </>
  );
};

export default DateFilter;
