import React from 'react';
import { SelectItem } from '../Select/SelectItem';
import { SelectFormProps } from './SelectFormProps';
import StyledSelectForm from './StyledSelectForm';

const SelectForm: React.FC<SelectFormProps> = ({ datasource, label, ...props }) => (
    <StyledSelectForm label={label} datasource={datasource} {...props}>
        {label && <option value='' />}
        {datasource.map((item: SelectItem) => (
            <option key={item.id} value={item.value}>
                {item.label}
            </option>
        ))}
    </StyledSelectForm>
);

export default SelectForm;
