import classNames from 'classnames';
import * as React from 'react';

interface PageItemProps
    extends React.DetailedHTMLProps<React.AnchorHTMLAttributes<HTMLAnchorElement>, HTMLAnchorElement> {
    isDisabled?: boolean;
}

const PageItem: React.FunctionComponent<PageItemProps> = ({
    children,
    onClick,
    href = '#',
    isDisabled = false,
    className = '',
    ...props
}: PageItemProps) => {
    const onClickProxy = onClick
        ? (event: React.MouseEvent<HTMLAnchorElement, MouseEvent>) => {
              event.preventDefault();
              onClick(event);
          }
        : (event: React.MouseEvent<HTMLAnchorElement, MouseEvent>) => event.preventDefault();

    const aClassNames = classNames({
        'pointer-events-none': isDisabled,
    });

    return (
        <a href={href} className={`${className} ${aClassNames}`} onClick={onClickProxy} {...props}>
            {children}
        </a>
    );
};

export default PageItem;
