import { Transition } from '@headlessui/react';
import { OverlayItemProps } from '@hornero/overlay';
import { Button } from '@hornero/ui';
import * as React from 'react';
import { ColumnDataType, ColumnModel, CompareOperators } from 'tubular-common';
import { QuickFilterModel } from '../DataGrid/QuickFilter/QuickFilter';
import { FiltersContainer } from './FiltersContainer';

export interface FiltersDrawerProps extends OverlayItemProps {
  columns: ColumnModel[];
  onApplyFeatures: (columns: ColumnModel[]) => void;
  togglePanel: () => void;
  open: boolean;
  quickFilters: QuickFilterModel[];
}

const resolveFilterOperator = (column: ColumnModel) => {
  if (column.filterOperator === CompareOperators.None) {
    return column.dataType === ColumnDataType.String
      ? CompareOperators.Contains
      : CompareOperators.Equals;
  }
  return column.filterOperator;
};

const copyColumns = (columns: ColumnModel[]): ColumnModel[] =>
  columns.map((column) => ({
    ...column,
    filterOperator: resolveFilterOperator(column),
  }));

export const FiltersDrawer: React.FunctionComponent<FiltersDrawerProps> = ({
  columns,
  quickFilters,
  onApplyFeatures,
  close,
}: FiltersDrawerProps) => {
  const [realOpen, setRealOpen] = React.useState(true);
  const tempColumns = copyColumns(columns);

  const onClose = () => {
    setRealOpen(false);
    setTimeout(() => {
      close();
    }, 301);
  };

  const onApplyClick = () => {
    onClose();
    onApplyFeatures(tempColumns);
  };

  return (
    <Transition
      appear
      show={realOpen}
      enter="ease-in-out duration-300"
      enterFrom="opacity-0"
      enterTo="opacity-100"
      leave="ease-in-out duration-300"
      leaveFrom="opacity-100"
      leaveTo="opacity-0"
      className="fixed z-10"
    >
      <div className="fixed inset-0 overflow-hidden">
        <div className="absolute inset-0 overflow-hidden">
          <div
            className="absolute inset-0 bg-gray-500 bg-opacity-75 transition-opacity"
            aria-hidden="true"
          />
          <section
            className="absolute inset-y-0 right-0 pl-10 max-w-full flex"
            aria-labelledby="slide-over-heading"
          >
            <Transition.Child
              enter="transform transition ease-in-out duration-300 sm:duration-300"
              enterFrom="translate-x-full"
              enterTo="translate-x-0"
              leave="transform transition ease-in-out duration-300 sm:duration-300 "
              leaveFrom="translate-x-0"
              leaveTo="translate-x-full"
            >
              <div className="relative w-screen max-w-xs h-full">
                <div className="h-full flex flex-col py-6 bg-white shadow-xl overflow-y-auto">
                  <div className="absolute right-0 -ml-8 pr-2 flex sm:-ml-10 sm:pr-4">
                    <button
                      type="submit"
                      onClick={onClose}
                      className="rounded-md text-gray-900 hover:text-gray-300 focus:outline-none focus:ring-2 focus:ring-white"
                    >
                      <span className="sr-only">Close panel</span>
                      <svg
                        className="h-6 w-6"
                        xmlns="http://www.w3.org/2000/svg"
                        fill="currentColor"
                        viewBox="0 0 24 24"
                        stroke="currentColor"
                        aria-hidden="true"
                      >
                        <path
                          strokeLinecap="round"
                          strokeLinejoin="round"
                          strokeWidth="2"
                          d="M6 18L18 6M6 6l12 12"
                        />
                      </svg>
                    </button>
                  </div>
                  <div className="px-4 sm:px-6">
                    <h2
                      id="slide-over-heading"
                      className="font-bold text-black inline-flex"
                    >
                      <svg
                        xmlns="http://www.w3.org/2000/svg"
                        className="h-6 pr-1 self-center"
                        fill="none"
                        viewBox="0 0 24 24"
                        stroke="currentColor"
                      >
                        <path
                          strokeLinecap="round"
                          strokeLinejoin="round"
                          strokeWidth="2"
                          d="M12 6V4m0 2a2 2 0 100 4m0-4a2 2 0 110 4m-6 8a2 2 0 100-4m0 4a2 2 0 110-4m0 4v2m0-6V4m6 6v10m6-2a2 2 0 100-4m0 4a2 2 0 110-4m0 4v2m0-6V4"
                        />
                      </svg>
                      More filters
                    </h2>
                  </div>
                  <div className="mt-6 relative flex-1 px-4 sm:px-6">
                    <FiltersContainer
                      filterableColumns={tempColumns.filter(
                        (c) =>
                          c.filterable &&
                          !c.isComputed &&
                          (!quickFilters ||
                            (quickFilters &&
                              !quickFilters.some((qf) => qf.column === c.name)))
                      )}
                      onApply={onApplyClick}
                    />
                  </div>
                  <div className="px-4 sm:px-6 mt-3 flex space-x-2 justify-end">
                    <Button onClick={onClose}>Cancel</Button>
                    <Button color="primary" onClick={onApplyClick}>
                      Apply filters
                    </Button>
                  </div>
                </div>
              </div>
            </Transition.Child>
          </section>
        </div>
      </div>
    </Transition>
  );
};

export default FiltersDrawer;
