import * as React from 'react';
import { useTbSelection, useTbTable, ITbGridProps } from 'tubular-react-common';
import { CompareOperators } from 'tubular-common';
import { Pagination } from '@hornero/pagination';
import { DataGridHeader } from './DataGridHeader';
import { DataGridBody } from './DataGridBody';
import { DataGridToolbar } from './DataGridToolbar';
import { DataGridRowDefaultProps } from './DataGridRowDefault';
import { QuickFilterModel } from './QuickFilter/QuickFilter';

export interface DataGridProps extends ITbGridProps {
    rowComponent?: React.FunctionComponent<DataGridRowDefaultProps>;
    quickFilters?: QuickFilterModel[];
}

export const DataGrid: React.FunctionComponent<DataGridProps> = ({
    columns,
    dataSource,
    gridName,
    rowSelectionEnabled = false,
    rowComponent,
    quickFilters,
}) => {
    const { state, api } = useTbTable(columns, dataSource);
    const selection = useTbSelection({ state, api }, true);

    const applyOrResetFilter = (columnName: string, value?: string) => {
        const newColumns = state.columns.map((column) => {
            if (column.name === columnName) {
                const hasValue = value !== null && value !== undefined;
                return {
                    ...column,
                    filterText: value,
                    filterOperator: hasValue ? CompareOperators.Equals : CompareOperators.None,
                    filterArgument: hasValue ? [] : null,
                };
            }

            return column;
        });

        api.setColumns(newColumns);
    };

    const resetAllFilters = () => {
        const newColumns = state.columns.map((column) => ({
            ...column,
            filterText: null,
            filterOperator: CompareOperators.None,
            filterArgument: null,
        }));
        api.setColumns(newColumns);
    };

    return (
        <div className='flex flex-col h-full'>
            <DataGridToolbar
                gridName={gridName}
                columns={state.columns}
                exportTo={api.exportTo}
                setColumns={api.setColumns}
                search={api.updateSearchText}
                searchText={state.searchText}
                refresh={api.reloadGrid}
                onClearFilters={resetAllFilters}
                quickFilters={quickFilters}
            />
            <div className='-my-2 sm:-mx-6 lg:-mx-8 h-full'>
                <div className='py-2 align-middle inline-block min-w-full sm:px-6 lg:px-8 h-full'>
                    <div className='shadow border-b border-gray-200 sm:rounded-lg h-full'>
                        <table className='min-w-full divide-y divide-gray-200 h-full' data-testid={`table-${gridName}`}>
                            <DataGridHeader
                                rowSelectionEnabled={rowSelectionEnabled}
                                selection={selection}
                                columns={state.columns}
                                sortColumn={api.sortColumn}
                                rows={state.data}
                                isLoading={state.isLoading}
                            />
                            <DataGridBody
                                itemsPerPage={state.itemsPerPage}
                                columns={state.columns}
                                data={state.data}
                                isLoading={state.isLoading}
                                rowSelectionEnabled={rowSelectionEnabled}
                                selection={selection}
                                rowComponent={rowComponent}
                            />
                        </table>
                        <Pagination
                            isLoading={state.isLoading}
                            filteredRecordCount={state.filteredRecordCount}
                            itemsPerPage={state.itemsPerPage}
                            page={state.page}
                            goToPage={api.goToPage}
                            updateItemsPerPage={api.updateItemsPerPage}
                        />
                    </div>
                </div>
            </div>
        </div>
    );
};

export default DataGrid;
