import * as React from 'react';
import tw from 'tailwind-styled-components';

interface ClearFilterButtonProps {
    disabled?: boolean;
    onClick: () => void;
}

const StyledClearFilterButton = tw.button`
${({ disabled }: ClearFilterButtonProps) => (disabled ? 'text-gray-400 cursor-default' : 'text-primary cursor-pointer')}
border-0
bg-transparent
`;

const ClearFilterButton: React.FC<ClearFilterButtonProps> = ({ disabled = false, onClick, children }) => (
    <StyledClearFilterButton onClick={onClick} disabled={disabled} data-testid='clearFilters'>
        {children}
    </StyledClearFilterButton>
);

export default ClearFilterButton;
