import * as React from 'react';
import tw from 'tailwind-styled-components';
import { DataGridSearchTextProps } from './DataGridSearchTextProps';
import useDebounce from '../../hooks';

interface CrossProps {
    visible: boolean;
}

const SearchBoxLayout = tw.div`
flex 
flex-row 
relative
`;

const SearchGlass = tw.svg`
h-6 
w-6
opacity-75
inset-y-0 
left-2 
self-center 
`;

const Input = tw.input`
flex-grow 
border-b-2 
border-black 
focus:border-primary 
focus:outline-none 
block 
h-full 
w-full 
ml-2 
pr-5 
pb-0 
sm:text-sm 
md:text-base
bg-transparent
`;

const Button = tw.button`
cursor-pointer
self-center
right-0
absolute
focus:outline-none 
bg-white 
hover:bg-gray-50
${({ visible }: CrossProps) => (visible ? 'visible' : 'invisible')}
`;

const Cross = tw.svg`
h-5 
w-5
opacity-75
`;

const DataGridSearchText: React.FunctionComponent<DataGridSearchTextProps> = ({
    searchText = '',
    search,
    placeholder = 'Search',
}: DataGridSearchTextProps) => {
    const [value, setValue] = React.useState(searchText);

    const debounced = useDebounce(search, 500);

    const onChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        setValue(event.target.value);
        debounced(event.target.value);
    };

    const onCrossPressed = (event: React.MouseEvent<HTMLInputElement>) => {
        setValue('');
        debounced('');
    };

    return (
        <SearchBoxLayout>
            <SearchGlass xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24' fill='none' stroke='currentColor'>
                <path
                    strokeLinecap='round'
                    strokeLinejoin='round'
                    strokeWidth={2}
                    d='M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z'
                />
            </SearchGlass>
            <Input value={value} onChange={onChange} type='text' placeholder={placeholder} />
            <Button onClick={onCrossPressed} visible={value !== ''} data-testid='cross'>
                <Cross xmlns='http://www.w3.org/2000/svg' viewBox='0 0 20 20' fill='currentColor'>
                    <path
                        fillRule='evenodd'
                        d='M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z'
                        clipRule='evenodd'
                    />
                </Cross>
            </Button>
        </SearchBoxLayout>
    );
};

export default DataGridSearchText;
