import tw from 'tailwind-styled-components';
import { ButtonProps } from './ButtonProps';

interface StyledButtonProps extends ButtonProps {
    colorstyle: {
        bgColor: 'bg-main' | 'bg-primary' | 'bg-secondary';
        borderColor: 'border-main' | 'border-primary' | 'border-secondary';
        hoverColor: 'hover:bg-main-hover' | 'hover:bg-primary-hover' | 'hover:bg-secondary-hover';
        hoverLightColor: 'hover:bg-main-hoverLight' | 'hover:bg-primary-hoverLight' | 'hover:bg-secondary-hoverLight';
        textColor: 'text-main' | 'text-primary' | 'text-secondary';
        contrastTextColor: 'text-main-contrast' | 'text-primary-contrast' | 'text-secondary-contrast';
    };
}

const getDisabledStyles = ({ variant, disabled, colorstyle }: StyledButtonProps) => {
    if (disabled) {
        return `
            cursor-default
            opacity-30
        `;
    }

    return `
        cursor-pointer 
        ${variant !== 'contained' ? colorstyle.hoverLightColor : colorstyle.hoverColor}
    `;
};

const getVariantStyles = ({ variant, colorstyle }: StyledButtonProps) => `
        ${variant === 'outlined' && colorstyle.borderColor}
        ${variant === 'contained' && colorstyle.bgColor}
    `;

const getTextColor = ({ variant, colorstyle }: StyledButtonProps) =>
    `${variant === 'contained' ? colorstyle.contrastTextColor : colorstyle.textColor}`;

const StyledButton = tw.button`
  border-transparent
  border 
  font-sans
  ${getTextColor}  
  font-medium
  py-2 
  px-4 
  rounded-none
  shadow-3xl
  focus:outline-none
  ${getDisabledStyles}
  ${getVariantStyles}
`;

export default StyledButton;
