import React, { useState } from 'react';
import { Checkbox, Button, TextInput } from '@hornero/ui';
import tw from 'tailwind-styled-components';
import { useAuth } from '@hornero/stores';

const LoginLayout = tw.div`
    flex-col
    w-full
    place-items-stretch
`;

const UtilsLayout = tw.div`
flex
flex-row
justify-between
w-full
`;

const Link = tw.a`
text-sm
`;

const ButtonStyled = tw(Button)`
w-full
`;

const WrongLoginDiv = tw.div`
mt-3
bg-red-200
rounded
border-2
border-red-600
w-full
`;

const WrongLoginMessage = tw.label`
text-red-600
text-lg
`;

function CoreUsersLoginForm() {
    const [user, setUser] = useState('');
    const [password, setPassword] = useState('');
    const [wrongUser, setWrongUser] = useState(false);

    const changeUser = (event: any) => {
        setUser(event.target.value);
    };

    const changePassword = (event: any) => {
        setPassword(event.target.value);
    };

    const authService = useAuth();

    const login = () => {
        authService
            .login(user, password)
            .then(() => (authService.errorWasRaised ? setWrongUser(true) : setWrongUser(false)));
    };
    return (
        <LoginLayout>
            <TextInput
                data-testid='email-textfield'
                placeholder='Email address'
                onChange={(event) => changeUser(event)}
            />
            <TextInput
                data-testid='password-textfield'
                placeholder='Password'
                onChange={(event) => changePassword(event)}
                type='password'
            />
            <UtilsLayout>
                <Checkbox id='checkboxRemember' label='Remember me' />
                <Link href='/'>Forgot your password?</Link>
            </UtilsLayout>
            <ButtonStyled
                data-testid='login-button'
                variant='contained'
                color='primary'
                disabled={!user || !password}
                onClick={() => login()}
            >
                Sign in
            </ButtonStyled>
            {wrongUser && (
                <WrongLoginDiv>
                    <WrongLoginMessage data-testid='error-message'>Wrong login credentials.</WrongLoginMessage>
                </WrongLoginDiv>
            )}
        </LoginLayout>
    );
}

export default CoreUsersLoginForm;
