import * as React from 'react';
import { ITbSelection } from 'tubular-react-common';
import { DataGridSelectCheckbox } from './DataGridSelectCheckbox';

export interface DataGridHeaderSelectionCellProps {
    selection?: ITbSelection;
    isLoading: boolean;
    rows: any[];
}

export const DataGridHeaderSelectionCell: React.FunctionComponent<DataGridHeaderSelectionCellProps> = ({
    selection,
    isLoading,
    rows,
}: DataGridHeaderSelectionCellProps) => {
    const toggleRowsSelection = () => selection.toggleAllRowsSelection();
    const isIndeterminate = selection.isIndeterminateSelection();
    const checked = selection.getUnSelectedCount() === 0 && rows.length > 0;
    return (
        <th
            scope='col'
            className='group px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider w-1'
        >
            <div className='relative inline-flex cursor-pointer'>
                <DataGridSelectCheckbox
                    checked={checked || null}
                    isIndeterminate={isIndeterminate}
                    disabled={isLoading}
                    onChange={toggleRowsSelection}
                />
            </div>
        </th>
    );
};

export default DataGridHeaderSelectionCell;
