import classNames from 'classnames';
import * as React from 'react';
import { Link, useRouteMatch } from 'react-router-dom';

export interface CoreAppLinkProps {
    label: string;
    to: string;
    activeOnlyWhenExact: boolean;
    isMobile?: boolean;
}

export const CoreAppLink: React.FunctionComponent<CoreAppLinkProps> = ({
    label,
    to,
    activeOnlyWhenExact,
    isMobile,
}: CoreAppLinkProps) => {
    const match = useRouteMatch({
        path: to,
        exact: activeOnlyWhenExact,
    });

    const linkClasses = classNames('text-white px-3 py-2 rounded-md text-sm font-medium', {
        'bg-gray-900': match,
        block: isMobile,
        'text-base': isMobile,
    });

    return (
        <Link to={to} className={linkClasses}>
            {label}
        </Link>
    );
};

export default CoreAppLink;
