import { ColumnModel } from 'tubular-common';
import * as React from 'react';
import { ITbSelection } from 'tubular-react-common';
import { DataGridRowDefault, DataGridRowDefaultProps } from './DataGridRowDefault';
import { DataGridBodyShimmer } from './DataGridBodyShimmer';

export interface DataGridBodyProps {
    columns: ColumnModel[];
    isLoading: boolean;
    data: any[];
    itemsPerPage: number;
    rowSelectionEnabled: boolean;
    selection: ITbSelection;
    rowComponent?: React.FunctionComponent<DataGridRowDefaultProps>;
}

export const DataGridBody: React.FunctionComponent<DataGridBodyProps> = ({
    isLoading,
    data,
    columns,
    itemsPerPage,
    rowSelectionEnabled,
    selection,
    rowComponent,
}: DataGridBodyProps) => {
    const RowComponent = rowComponent || DataGridRowDefault;
    if (isLoading) {
        return <DataGridBodyShimmer columns={columns} itemsPerPage={itemsPerPage} />;
    }

    return (
        <tbody className='bg-white block overflow-y-auto h-full'>
            {!isLoading &&
                data.map((row, index) => (
                    <RowComponent
                        rowSelectionEnabled={rowSelectionEnabled}
                        selection={selection}
                        isLoading={isLoading}
                        row={row}
                        rowIndex={index}
                        columns={columns}
                        key={row}
                    />
                ))}
        </tbody>
    );
};

export default DataGridBody;
