import React, { useState } from 'react';
import tw from 'tailwind-styled-components';
import { SelectProps } from './SelectProps';

const DivSelect = tw.div`
flex
flex-row
items-center
relative
mt-1 
block 
border 
border-gray-500
bg-white
shadow-sm 
rounded-sm
${({ error }: SelectProps) => error && 'border-red-400'}
`;

const Select = tw.select`
flex-grow
self-stretch
appearance-none
rounded-sm
focus:outline-none
focus:ring-primary
focus:border-primary
focus:ring-2
p-1
${(s) => s.extraClasses}
${({ error }: SelectProps) => error && 'bg-red-100'}`;

const Placeholder = tw.label`
text-gray-400
text-sm
absolute
left-1
pointer-events-none
`;

const Arrow = tw.svg`
absolute
text-gray-400
fill-current
right-0
py-1
w-5
h-5
pointer-events-none
`;

const Alert = tw.svg`
absolute
right-4
stroke-current
bg-transparent
text-red-400
mx-2
w-5
h-5
${({ error }: SelectProps) => (error ? 'visible' : 'invisible')}
`;

const StyledSelect: React.FC<SelectProps> = ({
    children,
    error,
    errorMessage,
    placeholder,
    disabled = false,
    name,
    onChange,
    ...props
}) => {
    const [showPlaceHolder, setShowPlaceHolder] = useState(true);
    const onChangeSelect = (event: any) => {
        setShowPlaceHolder(!event.target.value);
        if (onChange) onChange(event);
    };

    return (
        <>
            <DivSelect error={error}>
                {showPlaceHolder && <Placeholder id='placeholder'>{placeholder}</Placeholder>}
                <Arrow viewBox='6 4 15 10'>
                    <path d='M5.293 7.293a1 1 0 011.414 0L10 10.586l3.293-3.293a1 1 0 111.414 1.414l-4 4a1 1 0 01-1.414 0l-4-4a1 1 0 010-1.414z' />
                </Arrow>
                <Select
                    data-testid='select'
                    disabled={disabled}
                    error={error}
                    onChange={onChangeSelect}
                    id={name}
                    name={name}
                    {...props}
                >
                    {children}
                </Select>
                <Alert fill='none' viewBox='0 0 24 24' error={error}>
                    <path
                        strokeLinecap='round'
                        strokeLinejoin='round'
                        strokeWidth={2}
                        d='M12 9v2m0 4h.01m-6.938 4h13.856c1.54 0 2.502-1.667 1.732-3L13.732 4c-.77-1.333-2.694-1.333-3.464 0L3.34 16c-.77 1.333.192 3 1.732 3z'
                    />
                </Alert>
            </DivSelect>
        </>
    );
};

export default StyledSelect;
