import React, { ReactElement, useEffect } from 'react';
import StyledModal from './StyledModal';
import ModalItem from './ModalItem';
import modalService from './ModalService';

const ModalOverlay: React.FC = () => {
    const [items, setItems] = React.useState([]);
    const onUpdate = () => {
        setItems([...modalService.getItems()]);
    };

    React.useEffect(() => {
        modalService.subscribe(onUpdate);
    }, []);

    const onCloseItem = (item: ModalItem) => (param: any) => {
        modalService.remove(item.id);
        item.resolve(param ? param.$value : null);
    };

    return (
        <div>
            {items.map((item) => {
                const WrappedComponent = item.component;
                return (
                    <StyledModal
                        key={item.id}
                        title={item.props.title}
                        color={item.props.color}
                        close={onCloseItem(item)}
                        modalClosable={item.props.modalClosable}
                    >
                        <WrappedComponent key={item.id} {...item.props} close={onCloseItem(item)} />
                    </StyledModal>
                );
            })}
        </div>
    );
};

export default ModalOverlay;
