import * as React from 'react';
import { IAuthStore } from './Auth';
import { IRootStore } from './RootStore';

interface IRootStoreContext {
    auth: IAuthStore;
}

export const RootStoreContext = React.createContext<IRootStoreContext>(null);

interface RootStoreProviderProps {
    rootStore: IRootStore;
    children: React.ReactNode;
}

export const RootStoreProvider = ({ children, rootStore }: RootStoreProviderProps): JSX.Element => (
    <RootStoreContext.Provider value={rootStore}>{children}</RootStoreContext.Provider>
);
