import * as React from 'react';
import { columnHasFilter, ColumnModel, CompareOperators } from 'tubular-common';
import { OverlayService } from '@hornero/overlay';
import tw from 'tailwind-styled-components';
import { FiltersDrawer } from '../Filters/FiltersDrawer';
import DataGridSearchText from './DataGridSearchText';
import RefreshButton from './RefreshButton';
import ClearFilterButton from './ClearFilterButton/ClearFilterButton';
import QuickFilter, { QuickFilterModel } from './QuickFilter/QuickFilter';

interface DataGridToolbarProps {
    search: (term: string) => void;
    gridName: string;
    searchText: string;
    columns: ColumnModel[];
    setColumns: (columns: ColumnModel[]) => void;
    exportTo: (allRows: boolean, exportFunc: (payload: any[], columns: ColumnModel[]) => void) => void;
    refresh: () => void;
    onClearFilters: () => void;
    quickFilters?: QuickFilterModel[];
}

export const DataGridToolbar: React.FunctionComponent<DataGridToolbarProps> = ({
    columns,
    setColumns,
    search,
    searchText,
    exportTo,
    gridName,
    refresh,
    onClearFilters,
    quickFilters,
}: DataGridToolbarProps) => {
    const [cleared, setCleared] = React.useState(false);

    const filter = (column: ColumnModel, operator: CompareOperators, argument1: any, argument2?: any) => {
        const newColumns = columns.map((col) => {
            if (col.name === column.name) {
                return {
                    ...col,
                    filterOperator: operator,
                    filterText: argument1,
                    filterArgument: argument2 ? [argument2] : null,
                };
            }

            return col;
        });

        setColumns(newColumns);
    };

    const onClearAllFilters = () => {
        setCleared(true);
        onClearFilters();
    };

    const onShowFiltersDrawer = () => {
        OverlayService.addItem(FiltersDrawer, {
            columns,
            onApplyFeatures,
            quickFilters,
        });
    };

    const applyFilters = (columnModels: ColumnModel[]): ColumnModel[] => {
        columnModels.forEach((fColumn) => {
            const column = columnModels.find((c: ColumnModel) => c.name === fColumn.name);

            if (columnHasFilter(fColumn)) {
                column.filterText = fColumn.filterText;
                column.filterOperator = fColumn.filterOperator;
                column.filterArgument = fColumn.filterArgument;

                if (
                    column.filterOperator === CompareOperators.Between &&
                    (!column.filterArgument || !column.filterArgument[0])
                ) {
                    column.filterOperator = CompareOperators.Gte;
                    column.filterArgument = null;
                }
            } else {
                column.filterText = null;
                column.filterOperator = CompareOperators.None;
                column.filterArgument = null;
            }
        });

        return columnModels;
    };

    const onApplyFeatures = (columnModels: ColumnModel[]) => {
        const newColumns = applyFilters(columnModels);
        setColumns(newColumns);
    };

    const onQuickFilterChange = (columnName: string, value: string) => {
        setCleared(false);
        const newColumns = columns.map((col) => {
            if (col.name === columnName) {
                if (value === 'All') {
                    return {
                        ...col,
                        filterOperator: CompareOperators.None,
                        filterText: null,
                        filterArgument: null,
                    };
                }
                return {
                    ...col,
                    filterOperator: CompareOperators.Equals,
                    filterText: value,
                    filterArgument: null,
                };
            }

            return col;
        });

        setColumns(newColumns);
    };

    const anyFilterApplied = (columnModels: ColumnModel[]) => columnModels.some((column) => columnHasFilter(column));

    const activeFilterStyle = () => {
        const filteredColumns = columns.filter(
            (c) =>
                columnHasFilter(c) && c.filterable && quickFilters && !quickFilters.some((qf) => qf.column === c.name),
        );
        if (filteredColumns.length > 0) {
            return 'bg-blue-800 bg-opacity-10';
        }
        return 'bg-white';
    };

    const MoreFiltersButton = tw.button`
  inline-flex 
  mr-2 
  items-center 
  px-2 
  py-2 
  text-sm 
  font-medium 
  text-gray-700 
  leading-tight 
  hover:bg-gray-200 
  focus:outline-none
  ${activeFilterStyle}
  `;

    return (
        <div className='flex flex-col mb-3'>
            <div className='flex justify-between'>
                <div className='flex justify-start'>
                    <DataGridSearchText searchText={searchText} search={search} />
                </div>
                <div className='inline-flex'>
                    <span className='xs:hidden sm:inline-flex space-x-2'>
                        {quickFilters &&
                            quickFilters.map((quickFilter: QuickFilterModel, index: number) => (
                                <QuickFilter
                                    column={quickFilter.column}
                                    label={quickFilter.label}
                                    options={quickFilter.options}
                                    onChange={onQuickFilterChange}
                                    cleared={cleared}
                                    data-testid={`quickfilter${index}`}
                                />
                            ))}
                        <MoreFiltersButton type='button' onClick={onShowFiltersDrawer} data-testid='morefilters'>
                            <svg
                                xmlns='http://www.w3.org/2000/svg'
                                className='h-6 pr-1'
                                fill='none'
                                viewBox='0 0 24 24'
                                stroke='currentColor'
                            >
                                <path
                                    strokeLinecap='round'
                                    strokeLinejoin='round'
                                    strokeWidth='2'
                                    d='M12 6V4m0 2a2 2 0 100 4m0-4a2 2 0 110 4m-6 8a2 2 0 100-4m0 4a2 2 0 110-4m0 4v2m0-6V4m6 6v10m6-2a2 2 0 100-4m0 4a2 2 0 110-4m0 4v2m0-6V4'
                                />
                            </svg>
                            More Filters
                        </MoreFiltersButton>
                        <RefreshButton refresh={refresh} />
                    </span>
                </div>
            </div>
            <div className='flex flex-row flex-initial'>
                <span className='ml-auto'>
                    <ClearFilterButton disabled={!anyFilterApplied(columns)} onClick={onClearAllFilters}>
                        Clear all
                    </ClearFilterButton>
                </span>
            </div>
        </div>
    );
};

export default DataGridToolbar;
