import * as React from 'react';
import { CompareOperators } from 'tubular-common';
import { FilterEditorProps } from './FilterEditorProps';

export const BooleanFilterEditor: React.FunctionComponent<FilterEditorProps> = ({ column }: FilterEditorProps) => {
    const columnParam = column;
    const onChoiceChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        const { value } = event.currentTarget;
        if (value === 'all') {
            columnParam.filterOperator = CompareOperators.None;
            columnParam.filterText = null;
            return;
        }

        columnParam.filterOperator = CompareOperators.Equals;
        columnParam.filterText = value;
    };
    return (
        <div className='p-2 space-y-4'>
            <div className='flex items-center'>
                <input
                    onChange={onChoiceChange}
                    name={`${columnParam.name}_filter`}
                    value='true'
                    id='trueRadio'
                    type='radio'
                    className='focus:ring-indigo-500 h-4 w-4 text-indigo-600 border-gray-300'
                />
                <label htmlFor='trueRadio' className='ml-3 block text-sm font-medium text-gray-700'>
                    True
                </label>
            </div>
            <div className='flex items-center'>
                <input
                    name={`${columnParam.name}_filter`}
                    value='false'
                    id='falseRadio'
                    type='radio'
                    className='focus:ring-indigo-500 h-4 w-4 text-indigo-600 border-gray-300'
                />
                <label htmlFor='falseRadio' className='ml-3 block text-sm font-medium text-gray-700'>
                    False
                </label>
            </div>
            <div className='flex items-center'>
                <input
                    name={`${columnParam.name}_filter`}
                    value='all'
                    id='allRadio'
                    type='radio'
                    className='focus:ring-indigo-500 h-4 w-4 text-indigo-600 border-gray-300'
                />
                <label htmlFor='allRadio' className='ml-3 block text-sm font-medium text-gray-700'>
                    All
                </label>
            </div>
        </div>
    );
};

export default BooleanFilterEditor;
