import * as React from 'react';
import { CompareOperators } from 'tubular-common';
import { FilterEditorProps } from './FilterEditorProps';
import { onKeyDown } from './utils';

export const NumericFilter = ({ column, onApply }: FilterEditorProps) => {
  const aColumn = column;
  const handleFilterChange = (isSecondInput?: boolean) => (
    event: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>
  ) => {
    const newValue = event.target.value;
    if (isSecondInput) {
      aColumn.filterArgument = [];
      aColumn.filterArgument[0] = newValue;
    } else {
      aColumn.filterText = newValue;
    }
  };

  const isBetween = column.filterOperator === CompareOperators.Between;

  return (
    <>
      <input
        placeholder={isBetween ? 'From' : 'Type a number'}
        defaultValue={column.filterText}
        onKeyDown={onKeyDown(onApply)}
        onChange={handleFilterChange()}
        type="number"
        className="pl-1 border border-gray-400 focus:outline-none focus:border-primary block w-full shadow-sm sm:text-sm"
      />
      {isBetween && (
        <input
          type="number"
          placeholder="To"
          onChange={handleFilterChange(true)}
          defaultValue={column.filterArgument ? column.filterArgument[0] : ''}
          onKeyDown={onKeyDown(onApply)}
          className="pl-1 ml-1 border border-gray-400 focus:outline-none focus:border-primary block w-full shadow-sm sm:text-sm"
        />
      )}
    </>
  );
};

export default NumericFilter;
