import * as React from 'react';
import CoreMenuItems from '../molecules/CoreMenuItems';
import { CoreMobileMenu } from '../molecules/CoreMobileMenu';
import { CoreMobileMenuButton } from '../molecules/CoreMobileMenuButton';
import { CoreUserMenu } from '../molecules/CoreUserMenu';

const CoreMainMenu: React.FunctionComponent = () => {
    const [isMobileMenuOpen, setIsMobileMenuOpen] = React.useState(false);
    const toggleMobileMenu = () => setIsMobileMenuOpen(!isMobileMenuOpen);
    return (
        <nav className='bg-gray-800'>
            <div className='max-w-9xl mx-auto px-4 sm:px-6 lg:px-8'>
                <div className='flex items-center justify-between h-16'>
                    <div className='flex items-center'>
                        <div className='flex-shrink-0'>
                            <img
                                className='h-8 w-8'
                                src='https://tailwindui.com/img/logos/workflow-mark-indigo-500.svg'
                                alt='Workflow'
                            />
                        </div>
                        <div className='hidden md:block'>
                            <div className='ml-10 flex items-baseline space-x-4'>
                                <CoreMenuItems />
                            </div>
                        </div>
                    </div>
                    <div className='hidden md:block'>
                        <div className='ml-4 flex items-center md:ml-6'>
                            {/* <!-- Profile dropdown --> */}
                            <div className='ml-3 relative'>
                                <CoreUserMenu />
                            </div>
                        </div>
                    </div>
                    <div className='-mr-2 flex md:hidden'>
                        <CoreMobileMenuButton toggleMobileMenu={toggleMobileMenu} isMobileMenuOpen={isMobileMenuOpen} />
                    </div>
                </div>
            </div>
            {isMobileMenuOpen && <CoreMobileMenu />}
        </nav>
    );
};

export default CoreMainMenu;
