import React from 'react';
import { SelectItem } from './SelectItem';
import { SelectProps } from './SelectProps';
import StyledSelect from './StyledSelect';

const Select: React.FC<SelectProps> = ({ datasource, placeholder, ...props }) => (
    <StyledSelect placeholder={placeholder} datasource={datasource} {...props}>
        {placeholder && <option data-testid='select-option' value='' />}
        {datasource.map((item: SelectItem) => (
            <option data-testid='select-option' key={item.id} value={item.value}>
                {item.label}
            </option>
        ))}
    </StyledSelect>
);

export default Select;
