import React from 'react';
import styled from 'styled-components';
import { DataGrid } from '@hornero/tubular';
import { AggregateFunctions, ColumnDataType, ColumnSortDirection, createColumn } from 'tubular-common';
import { useTranslation } from 'react-i18next';
import CustomRow from '../molecules/CustomRow';

/* eslint-disable-next-line */
export interface UsersListProps {}

const StyledUsersList = styled.div`
    color: pink;
`;

const columns = [
    createColumn('OrderID', {
        dataType: ColumnDataType.Numeric,
        filterable: true,
        isKey: true,
        label: 'Id',
        sortDirection: ColumnSortDirection.Ascending,
        sortOrder: 1,
        sortable: true,
    }),
    createColumn('CustomerName', {
        aggregate: AggregateFunctions.Count,
        filterable: true,
        searchable: true,
        sortable: true,
    }),
    createColumn('ShippedDate', {
        dataType: ColumnDataType.DateTimeUtc,
        filterable: true,
        sortable: true,
    }),
    createColumn('ShipperCity'),
    createColumn('Amount', {
        dataType: ColumnDataType.Numeric,
        sortable: true,
    }),
    createColumn('IsShipped', {
        dataType: ColumnDataType.Boolean,
        filterable: true,
        sortable: true,
    }),
    createColumn('Actions', {
        dataType: ColumnDataType.String,
        filterable: false,
        sortable: false,
    }),
];

const quickFilters = [
    {
        column: 'CustomerName',
        label: 'Customer',
        options: [
            { id: 1, value: 'Oxxo' },
            { id: 2, value: 'Unosquare LLC' },
        ],
    },
    {
        column: 'ShipperCity',
        label: 'City',
        options: [{ id: 1, value: 'Guadalajara, JAL, Mexico' }],
    },
];

export function CoreUsersList(props: UsersListProps) {
    const { t } = useTranslation(['users', 'common']);
    return (
        <StyledUsersList>
            <h1>{t('users:Users title')}</h1>
            <div className='h-96'>
                <DataGrid
                    gridName='employees'
                    columns={columns}
                    dataSource='https://tubular.azurewebsites.net/api/orders/paged'
                    rowComponent={CustomRow}
                    quickFilters={quickFilters}
                />
            </div>
        </StyledUsersList>
    );
}

export default CoreUsersList;
