import React from 'react';
import tw from 'tailwind-styled-components';

const ErrorMessage = tw.label`
text-red-400
text-xs
-bottom-4
${({ error }: InputErrorMessageProps) => (error ? 'visible' : 'invisible')}
`;

export interface InputErrorMessageProps {
    error?: boolean;
    errorMessage?: string;
}

const InputErrorMessage: React.FC<InputErrorMessageProps> = ({ error, errorMessage }) => (
    <ErrorMessage error={error}>{errorMessage}</ErrorMessage>
);

export default InputErrorMessage;
