import * as React from 'react';
import { ITbRowProps } from 'tubular-react-common';
import tw from 'tailwind-styled-components';
import { DataGridSelectionCell } from './DataGridSelectionCell';
import renderCell from './utils';

export const TableRow = tw.tr`
md:table
md:table-fixed
md:w-full
${(p) => (p.rowIndex % 2 ? `bg-gray-100` : ``)}
`;

export interface DataGridRowDefaultProps extends ITbRowProps {
    isLoading: boolean;
}

export const DataGridRowDefault: React.FunctionComponent<DataGridRowDefaultProps> = ({
    columns,
    row,
    rowSelectionEnabled,
    selection,
    isLoading,
    rowIndex,
}: DataGridRowDefaultProps) => (
    <TableRow rowIndex={rowIndex}>
        {rowSelectionEnabled && (
            <DataGridSelectionCell isLoading={isLoading} selection={selection} row={row} columns={columns} />
        )}
        {columns.filter((col) => col.visible).map((col, index) => renderCell(col, row[col.name], index))}
    </TableRow>
);

export default DataGridRowDefault;
