import * as React from 'react';
import { ColumnModel } from 'tubular-common';
import tw from 'tailwind-styled-components';

export const TextCell = tw.td`
px-6 
py-3 
whitespace-nowrap
text-gray-400
text-xs
${(p) => p.textAlign}
${(p) => (p.colIndex === 0 ? `font-bold` : ``)}
`;

export interface DataGridTextCellProps {
    column: ColumnModel;
    text: string;
    textAlign?: string;
    colIndex: number;
}

export const DataGridTextCell: React.FunctionComponent<DataGridTextCellProps> = ({
    column,
    text,
    textAlign = 'text-left',
    colIndex,
}: DataGridTextCellProps) => (
    <TextCell role='cell' key={column.name} textAlign={textAlign} colIndex={colIndex}>
        {text}
    </TextCell>
);

export default DataGridTextCell;
