import React from 'react';
import tw from 'tailwind-styled-components';

const StyledLabel = tw.label`
flex
flex-row
text-sm 
md:text-base
text-black
items-center
`;

const CircleViewport = tw.svg`
w-2.5
h-2.5
ml-1
${({ required }: LabelProps) => (required ? 'visible' : 'invisible')}
`;

export interface LabelProps {
    required?: boolean;
    label?: string;
    htmlFor?: string;
}

const Label: React.FC<LabelProps> = ({ label, required, htmlFor }) => (
    <StyledLabel htmlFor={htmlFor}>
        {label}
        <CircleViewport viewBox='0 0 20 20' required={required}>
            <title>RequiredSVG</title>
            <circle cx='50%' cy='50%' r='5' fill='red' />
        </CircleViewport>
    </StyledLabel>
);

export default Label;
