import { useState } from 'react';

const useDebounce = (func, wait) => {
    const [timer, setTimer] = useState(null);
    return (...args: any[]) => {
        if (timer) clearTimeout(timer);
        setTimer(
            setTimeout(() => {
                setTimer(null);
                func(...args);
            }, wait),
        );
    };
};

export default useDebounce;
